"use client";

import React, { useState, useEffect, useMemo } from 'react';
import { DashboardLayout } from '@/components/layout/DashboardLayout';
import { Button } from '@/components/ui/button';
import { ArrowLeft, Receipt, Building, TrendingUp, Info } from 'lucide-react';
import { useRouter } from 'next/navigation';
import { format } from 'date-fns';
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from '@/components/ui/table';
import { Badge } from '@/components/ui/badge';
import { Skeleton } from '@/components/ui/skeleton';
import { 
  BarChart, Bar, XAxis, YAxis, CartesianGrid, Tooltip, ResponsiveContainer, 
  PieChart, Pie, Cell, Legend 
} from 'recharts';
import { cn } from '@/lib/utils';

export default function ProjectDetailsPage({ params }: { params: Promise<{ id: string }> }) {
  const { id } = React.use(params);
  const [project, setProject] = useState<any>(null);
  const [loading, setLoading] = useState(true);
  const router = useRouter();

  useEffect(() => {
    fetch(`/api/projects/${id}`)
      .then(res => res.json())
      .then(data => {
        setProject(data);
        setLoading(false);
      });
  }, [id]);

  const formatCurrency = (amount: number) => 
    new Intl.NumberFormat('en-BD', { style: 'currency', currency: 'BDT', maximumFractionDigits: 0 }).format(amount);

  // --- Intelligence Narrative Logic ---
  const narrative = useMemo(() => {
    if (!project) return "";

    const total = Number(project.totalProjectValue || 0);
    const bills = project.bills || [];
    const received = bills.reduce((sum: number, b: any) => sum + Number(b.receivedAmount || 0), 0);
    const paidCount = bills.filter((b: any) => b.status === 'PAID').length;
    const partialCount = bills.filter((b: any) => b.status === 'PARTIAL').length;
    const pendingCount = bills.filter((b: any) => b.status === 'PENDING').length;
    
    const recPct = ((received / total) * 100).toFixed(1);
    const nextBill = bills.find((b: any) => b.status !== 'PAID');

    let text = `The project "${project.projectName}" has a total valuation of ${formatCurrency(total)}. `;
    text += `To date, we have successfully realized ${recPct}% of the total budget. `;

    if (partialCount > 0) {
      text += `Currently, ${partialCount} billing phase(s) are in a "Partial" state, indicating ongoing collections or remaining BDT values within active milestones. `;
    }

    if (nextBill) {
      text += `The immediate focus is the "${nextBill.billName}" milestone, valued at ${formatCurrency(nextBill.billAmount)}, which represents ${nextBill.billPercent}% of the project value. `;
    }

    const remaining = total - received;
    text += `With ${paidCount} milestones fully cleared, the remaining exposure is ${formatCurrency(remaining)}.`;

    return text;
  }, [project]);

  if (loading) return <div className="p-10 space-y-6"><Skeleton className="h-32 w-full" /><Skeleton className="h-64 w-full" /></div>;

  return (
    <DashboardLayout title="Project Intelligence" currentPath="/projects" onNavigate={(path) => router.push(path)}>
      <div className="space-y-6">
        <Button variant="ghost" onClick={() => router.push('/projects')} className="gap-2 -ml-2 text-muted-foreground hover:text-foreground">
          <ArrowLeft className="w-4 h-4" /> Back to List
        </Button>

        {/* Narrative Summary Section */}
        <div className="relative overflow-hidden glass-card p-8 rounded-2xl border border-primary/20 bg-gradient-to-br from-primary/5 via-transparent to-transparent">
          <div className="relative z-10">
            <div className="flex items-center gap-2 mb-4">
              <div className="p-2 bg-primary/10 rounded-lg">
                <Info className="w-5 h-5 text-primary" />
              </div>
              <h3 className="text-sm font-bold uppercase tracking-widest text-primary">Billing Intelligence Narrative</h3>
            </div>
            <p className="text-xl md:text-2xl font-medium leading-relaxed text-foreground/90 max-w-5xl italic font-serif">
              "{narrative}"
            </p>
          </div>
          {/* Background Decorative Element */}
          <div className="absolute -right-16 -bottom-16 w-64 h-64 bg-primary/5 rounded-full blur-3xl" />
        </div>

        {/* Financial Metrics Row */}
        <div className="grid grid-cols-1 lg:grid-cols-4 gap-6">
          <div className="lg:col-span-3 glass-card p-6 rounded-xl flex flex-col justify-between">
            <div className="flex justify-between items-start mb-6">
              <div>
                <p className="text-xs text-muted-foreground uppercase font-bold tracking-widest mb-1">Project Identifier</p>
                <h2 className="text-3xl font-black text-foreground">{project.projectName}</h2>
              </div>
              <Badge className="bg-success/20 text-success border-success/30 px-3 py-1">
                {project.bills?.every((b: any) => b.status === 'PAID') ? 'FULLY REALIZED' : 'ACTIVE COLLECTION'}
              </Badge>
            </div>
            
            <div className="grid grid-cols-1 md:grid-cols-3 gap-8">
              <div className="space-y-1">
                <p className="text-xs text-muted-foreground font-medium">Primary Client</p>
                <p className="font-bold flex items-center gap-2 text-lg"><Building className="w-4 h-4 text-primary" /> {project.client?.name}</p>
              </div>
              <div className="space-y-1">
                <p className="text-xs text-muted-foreground font-medium">Total Contract Value</p>
                <p className="text-2xl font-black text-primary">{formatCurrency(Number(project.totalProjectValue))}</p>
              </div>
              <div className="space-y-1">
                <p className="text-xs text-muted-foreground font-medium">Realized Revenue</p>
                <p className="text-2xl font-black text-success">
                  {formatCurrency(project.bills?.reduce((s: number, b: any) => s + Number(b.receivedAmount || 0), 0))}
                </p>
              </div>
            </div>
          </div>

          <div className="glass-card p-6 rounded-xl border-t-4 border-primary flex flex-col items-center justify-center text-center">
             <p className="text-xs font-bold text-muted-foreground uppercase mb-4">Collection Progress</p>
             <div className="relative h-32 w-32 flex items-center justify-center">
                <ResponsiveContainer width="100%" height="100%">
                  <PieChart>
                    <Pie 
                      data={[
                        { name: 'Rec', value: project.bills?.reduce((s: number, b: any) => s + Number(b.receivedAmount || 0), 0) },
                        { name: 'Due', value: Number(project.totalProjectValue) - project.bills?.reduce((s: number, b: any) => s + Number(b.receivedAmount || 0), 0) }
                      ]} 
                      innerRadius={40} 
                      outerRadius={55} 
                      paddingAngle={8} 
                      dataKey="value"
                    >
                      <Cell fill="hsl(173, 80%, 36%)" />
                      <Cell fill="hsl(var(--muted))" />
                    </Pie>
                  </PieChart>
                </ResponsiveContainer>
                <div className="absolute inset-0 flex items-center justify-center">
                   <span className="text-xl font-black">
                    {Math.round((project.bills?.reduce((s: number, b: any) => s + Number(b.receivedAmount || 0), 0) / Number(project.totalProjectValue)) * 100)}%
                   </span>
                </div>
             </div>
          </div>
        </div>

        {/* Timeline Visualization */}
        <div className="glass-card p-6 rounded-xl">
          <div className="flex items-center gap-2 mb-8">
            <TrendingUp className="w-5 h-5 text-primary" />
            <h3 className="font-bold uppercase tracking-wider text-sm">Billing Realization Timeline</h3>
          </div>
          <div className="h-80">
            <ResponsiveContainer width="100%" height="100%">
              <BarChart data={project.bills?.map((b: any) => ({
                name: b.billName,
                total: Number(b.billAmount),
                rec: Number(b.receivedAmount)
              }))}>
                <CartesianGrid strokeDasharray="3 3" vertical={false} stroke="hsl(var(--border))" />
                <XAxis dataKey="name" axisLine={false} tickLine={false} tick={{ fontSize: 11, fontWeight: 600 }} />
                <YAxis axisLine={false} tickLine={false} tick={{ fontSize: 11 }} tickFormatter={(val) => `à§³${(val/1000).toFixed(0)}k`} />
                <Tooltip 
                  cursor={{fill: 'hsl(var(--primary)/0.05)'}}
                  contentStyle={{ borderRadius: '12px', border: '1px solid hsl(var(--border))', boxShadow: '0 10px 15px -3px rgb(0 0 0 / 0.1)' }}
                  formatter={(val: number) => formatCurrency(val)} 
                />
                <Legend iconType="circle" />
                <Bar name="Target Amount" dataKey="total" fill="hsl(var(--primary)/0.2)" radius={[6, 6, 0, 0]} />
                <Bar name="Realized Revenue" dataKey="rec" fill="hsl(173, 80%, 36%)" radius={[6, 6, 0, 0]} />
              </BarChart>
            </ResponsiveContainer>
          </div>
        </div>

        {/* Milestone Table */}
        <div className="glass-card rounded-2xl overflow-hidden border border-border/50 shadow-sm">
          <div className="p-5 border-b bg-muted/20 flex items-center justify-between">
            <div className="flex items-center gap-2">
              <Receipt className="w-5 h-5 text-primary" />
              <h3 className="font-bold">Milestone Breakdown</h3>
            </div>
          </div>
          <Table>
            <TableHeader className="bg-muted/10">
              <TableRow>
                <TableHead className="font-bold">Phase Description</TableHead>
                <TableHead className="text-center font-bold">Allocation</TableHead>
                <TableHead className="font-bold">Expected (BDT)</TableHead>
                <TableHead className="font-bold">Received (BDT)</TableHead>
                <TableHead className="font-bold">Collection Status</TableHead>
              </TableRow>
            </TableHeader>
            <TableBody>
              {project.bills?.map((bill: any) => (
                <TableRow key={bill.id} className="hover:bg-muted/5 transition-colors">
                  <TableCell className="font-semibold py-4">{bill.billName}</TableCell>
                  <TableCell className="text-center">
                    <Badge variant="outline" className="font-mono">{bill.billPercent}%</Badge>
                  </TableCell>
                  <TableCell className="font-medium">{formatCurrency(Number(bill.billAmount))}</TableCell>
                  <TableCell className="text-success font-bold">{formatCurrency(Number(bill.receivedAmount))}</TableCell>
                  <TableCell>
                    <Badge 
                      className={cn(
                        "font-bold",
                        bill.status === 'PAID' ? "bg-success/10 text-success border-success/20" : 
                        bill.status === 'PARTIAL' ? "bg-warning/10 text-warning border-warning/20" : 
                        "bg-muted text-muted-foreground"
                      )}
                      variant="outline"
                    >
                      {bill.status}
                    </Badge>
                  </TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </div>
      </div>
    </DashboardLayout>
  );
}